import { Menu } from "../components/menu/Menu";

export const AppRouter = () => {
  /*
  const routes = useRoutes([
    { path: '/', element: <Luzu3dApp /> },
    { path: 'visor/:nombreProducto', element: <ProductoVisor /> }
]);

return routes;*/

  return (
    <Router>
      <Menu />
      <Routes>
        <Route path="/" element={<Home />} />
        <Route path="visor/:nombreProducto" element={<ProductoVisor />} />
      </Routes>
    </Router>
  );
};
