import React from "react";
import { Link } from "react-router-dom";


export const ProductoTarjeta = ({
  codigo,
  nombre,
  descripcion,
  //alto,
  //ancho,
  peso,
  precio,
  aplica_descuento,
  precio_descuento,
  //estatus,
}) => {
  const imagenPath = `/assets/image/producto/${codigo}.jpg`;

  return (
   
    <div className="col">
      
      <div className="card shadow-sm">
        <img src={imagenPath} alt={nombre} />

        <div className="card-body">
          <p className="card-text">
            <strong>{nombre}</strong> <br />
            <small className="text-sm-start">{descripcion}</small>
          </p>
          <div className="d-flex justify-content-between align-items-center">
            <div className="btn-group">
              
                <Link to={`/visor/${nombre}`}>Mas</Link>
              
            </div>
            {aplica_descuento && (
              <small className="alert alert-danger text-decoration-line-through">
                Antes: Q. {precio}
              </small>
            )}
            <small className="alert alert-success">
              Precio: Q. {aplica_descuento === true ? precio_descuento : precio}
            </small>
          </div>
        </div>
      </div>
   
    </div>
  );
};
