import React from "react";
import { getProductosListado } from "../../selectors/getProductosListado";


import "./lista-galeria.css";
import { ProductoTarjeta } from "./ProductoTarjeta";

export const ListadoGaleria = () => {

  const productos = getProductosListado();

  return (
    <div className="album py-5 bg-light animate__animated animate__bounce" id="galeria">
      <div className="container">
        <div className="row row-cols-1 row-cols-sm-2 row-cols-md-3 g-3">
          {
            productos.map((producto)=> (
              <ProductoTarjeta
                key={producto.codigo}
                {...producto}
              />
            ))
            }
        </div>
      </div>
    </div>
  );
};
