import React, { useMemo } from "react";
import { Navigate } from "react-router-dom";
import { useNavigate, useParams } from "react-router-dom";
import { getProductoPorCodigo } from "../../selectors/getProductoPorCodigo";

export const ProductoVisor = () => {
  const { nombreProducto } = useParams();
  const navigate = useNavigate();

  const producto = useMemo(() => getProductoPorCodigo(nombreProducto), [
    nombreProducto,
  ]);

  if (!producto) {
    return <Navigate to="/" />;
  }

  const {
    codigo,
    nombre,
    descripcion,
    alto,
    ancho,
    peso,
    precio,
    aplica_descuento,
    precio_descuento,
    //estatus,
  } = producto;

  const imagenPath = `/assets/image/producto/${codigo}.jpg`;

  const handleReturn = (e) => {
    navigate(-1);
  };

  const precioFinal = aplica_descuento === true ? precio_descuento : precio;

  return (
    <div className="container">

    <div className="row mt-5 pt-5">
      <div className="col-4">
        <img
          src={imagenPath}
          alt={nombre}
          className="img-thumbnail animate__animated animate__bounceInLeft"
        />
      </div>

      <div className="col-8 animate__animated animate__fadeIn">
        <h3>{nombre}</h3>
        <ul className="list-group list-group-flush">
          <li className="list-group-item">
            <b>DescripciÃ³n:</b> {descripcion}
          </li>
          <li className="list-group-item">
            <b>Alto: </b> {`${alto} cm.`}
            <br />
            <b>Ancho: </b> {`${ancho} cm.`}
            <br />
            <b>Peso: </b> {`${peso} cm.`}
            <br />
          </li>
          <li className="list-group-item">
            {aplica_descuento && (
              <small className=" bg-warning text-decoration-line-through px-2 rounded-pill">
                Antes: Q. {precio}
              </small>
            )}
            <br />
            <small className="bg-info px-2 rounded-pill">
              &nbsp; Precio: Q.{" "}
              {aplica_descuento === true ? precio_descuento : precio}
            </small>
          </li>
          <li className="list-group-item "></li>
        </ul>
        <div className="btn-group mt-3" role="group" aria-label="Basic example">
          <a
            className="btn btn-outline-success"
            href={`https://wa.me/50255445461?text=Me podrÃ­a brindar mÃ¡s informaciÃ³n del producto *${nombre}*, con el precio de Q. ${precioFinal}.`}
          >
            Comprar
          </a>
          <button className="btn btn-outline-info" onClick={handleReturn}>
            Regresar
          </button>
        </div>
      </div>
    </div>
    </div>
  );
};
